import store from "@/store";
import { ObjectType } from "@/types";
import { computed, ref, watch } from "vue";

export default function useForm(): {
  config: ObjectType;
  commitChange: () => void;
} {
  const config = ref<ObjectType>({});
  const curData = computed(() => store.state.curComponent);
  watch(
    () => curData.value,
    (cur) => {
      config.value = { ...cur?.props };
    },
    { immediate: true }
  );
  function commitChange() {
    store.commit(
      "UPDATE_COMPONENT",
      Object.assign(curData.value, { props: config.value })
    );
  }
  return { config, commitChange };
}
