import { Component, h, VNode } from "vue";
import {
  NButton,
  NCard,
  NDataTable,
  NDatePicker,
  NIcon,
  NInput,
  NSelect,
} from "naive-ui";
import MyForm from "@/components/my-form.vue";
import * as Icons from "@vicons/ionicons5";
import { FormItemConfig, MenuItem, ObjectType } from "@/types";

/**
 * 根据配置生成组件
 * @param config 配置
 */
export function config2component(
  config: { name: string } & ObjectType,
  parent?: {
    set: (val: unknown) => void;
    emit: (type: string) => void;
  }
): Component {
  const { name, props = {} } = config;
  switch (name) {
    case "Button":
      return h(
        NButton,
        {
          "on-click": () => {
            parent?.emit("button");
          },
          ...props,
        },
        { default: () => props?.value || "按钮" }
      );
    case "Card":
      return h(NCard, { ...props }, { default: () => props?.value });
    case "Input":
      return h(
        NInput,
        {
          "on-input": (val: unknown) => parent?.set(val),
          ...props,
        },
        { default: () => props?.value }
      );
    case "DatePicker":
      return h(
        NDatePicker,
        {
          "on-update:value": (val: unknown) => parent?.set(val),
          ...props,
        },
        { default: () => props?.value }
      );
    case "Select":
      return h(
        NSelect,
        {
          "on-update:value": (val: unknown) => parent?.set(val),
          options: [],
          ...props,
        },
        { default: () => props?.value }
      );
    case "Form":
      return h(MyForm, { ...props });
    case "Table":
      return h(NDataTable, { ...props });
    case "Text":
      return h("p", { style: { margin: 0 } }, { default: () => props?.value });
    default:
      return h("div");
  }
}

/**
 * 生成唯一时间戳
 */
export function uni_code(): string {
  return new Date().getTime() + "";
}

type IconName = keyof typeof Icons;
/**
 * 根据icon名称渲染图标
 * @param icon 图标名称
 */
function renderIcon(icon: string): () => VNode {
  return () => h(NIcon, null, { default: () => h(Icons[icon as IconName]) });
}
/**
 * 将菜单数组组合成树结构
 * @param arr 菜单数组
 * @param parent 初始父节点key（默认为undefined）
 */
export function array2tree(arr: MenuItem[], parent?: string): MenuItem[] {
  const data: MenuItem[] = JSON.parse(JSON.stringify(arr));
  data.forEach((el) => {
    if (el?.icon && typeof el?.icon === "string") {
      el.icon = renderIcon(el.icon);
    }
  });
  function loop(p?: string) {
    return [...data].reduce((acc, cur: MenuItem) => {
      if (p === cur?.parent) {
        const children = loop(cur.key);
        cur.children = children?.length > 0 ? children : undefined;
        acc.push(cur);
      }
      return acc;
    }, [] as MenuItem[]);
  }
  return loop(parent);
}

/**
 * 根据配置返回校验的type值
 * @param item 表单项配置
 */
export function checkRuleType(
  item: FormItemConfig
): "array" | "number" | undefined {
  const { name, type, multiple } = item.component;
  switch (name) {
    case "DatePicker":
      return type?.includes("range") ? "array" : "number";
    case "Select":
      return multiple ? "array" : undefined;
    default:
      return undefined;
  }
}
