import { Config, MenuItem } from "@/types";
import { uni_code } from "@/utils";
import { nextTick } from "vue";
import { GlobalStateProps } from "./index";

export default {
  SET_CONFIGS(state: GlobalStateProps, data: Config[] = []): void {
    state.configs = JSON.parse(JSON.stringify(data));
  },
  SAVE_LAST_CONFIGS(state: GlobalStateProps): void {
    state.lastConfigs = JSON.stringify(state.configs);
  },
  ADD_COMPONENT(state: GlobalStateProps, data: Config): void {
    state.configs.push(data);
  },
  async UPDATE_COMPONENT(
    state: GlobalStateProps,
    data: Partial<Config>
  ): Promise<void> {
    const index = state.configs.findIndex((item) => item.i === data?.i);
    if (index < 0) return;
    const code = uni_code();

    function setData(newData: Partial<Config>) {
      state.configs.splice(
        index,
        1,
        Object.assign(state.configs[index], data, newData)
      );
    }

    setData({ i: code });
    await nextTick();
    /** 根据元素高度重设容器h */
    const el = document.querySelector<HTMLElement>(`._el_${code}`);
    if (!el) return;

    setData({
      i: uni_code(),
      h: Math.ceil(el?.offsetHeight / 50),
    });
  },
  CLEAR(state: GlobalStateProps): void {
    state.configs = [];
    state.curComponent = {} as Config;
    state.showConfigDrawer = false;
  },
  SET_CUR_COMPONENT(state: GlobalStateProps, data: Config): void {
    state.curComponent = data;
  },
  SWITCH_CONFIG_DRAWER(state: GlobalStateProps, val: boolean): void {
    state.showConfigDrawer = val;
  },
  ADD_MENU(state: GlobalStateProps, data: MenuItem): void {
    state.menus.push({ ...data });
  },
  SET_CUR_MENU(state: GlobalStateProps, val: string): void {
    state.curMenuKey = val;
  },
  SAVE_PAGE(state: GlobalStateProps): void {
    if (!state.curMenuKey) return;
    state.pages[state.curMenuKey] = JSON.parse(JSON.stringify(state.configs));
  },
  DELETE_PAGE(state: GlobalStateProps, key: string): void {
    delete state.pages[key];
  },
};
